  width: 353px;
  height: 425px;
  border: 6px solid #f0ede8;
  box-sizing: border-box;
  margin: 0 auto;
  margin-right: 132px; }
  .product__photo-frame img {
    position: absolute;
    max-width: none;
    left: 50%;
    bottom: 40px;
    transform: translateX(-52%); }
  .product__photo-frame .product__increase::before {
    content: "";
    width: 50px;
    height: 50px;
    display: block;
    background: url("../img/lens-icon.svg") #9b8869 center no-repeat;
    position: absolute;
    bottom: -6px;
    left: 50%;
    transform: translate(-50%, 50%); }
  .product__photo-frame .product__compare::before {
    content: "";
    width: 30px;
    height: 20px;
    display: block;
    background: url("../img/compare.svg") top left no-repeat;
    position: absolute;
    bottom: 7px;
    right: 7px; }

.product__color {
  display: flex;
  margin: 0 auto;
  width: fit-content;
  position: absolute;
  bottom: 44px;
  left: 50%;
  transform: translateX(-50%); }

.product__color-button {
  width: 20px;
  height: 20px;
  padding: 0;
  margin-right: 10px;
  border: none;
  border-radius: 50%;
  position: relative; }
  .product__color-button:focus {
    outline: none; }
  .product__color-button:last-child {
    margin-right: 0; }
  .product__color-button--black {
    background-color: #333232; }
  .product__color-button--brown {
    background-color: #7e5b48; }
  .product__color-button--golden {
    background-color: #d5b688; }
  .product__color-button--active::after {
    content: "";
    width: 30px;
    height: 30px;
    position: absolute;
    display: block;
    border-radius: 50%;
    border: 2px solid #9b8869;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); }

.product__description {
  width: 635px; }

